<template>
    <div class="retrieve">
        <van-nav-bar
            title="找回密码"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />

        <van-form @submit="onSubmit">
            <van-field
                v-model="userInfo.phone"
                type="text"
                name="手机号"
                label="手机号"
                placeholder="注册时手机号"
            />

            <van-field
                v-model="userInfo.password"
                :type="flag ? 'password' : 'text'"
                name="新密码"
                label="新密码"
                placeholder="密码(6-16位)"
                @click-right-icon="flag = !flag"
            >
                <template v-slot:right-icon>
                    <i class="fa fa-lg" aria-hidden="true" :class="[flag ? 'fa-eye-slash' : 'fa-eye']"></i>
                </template>
            </van-field>

            <div class="btn">
                <van-button round block type="info" native-type="submit">
                提交
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    import { Toast } from 'vant';
    import validForm from "../assets/js/validForm";

    export default {
        data() {
            return {
                flag: true,

                userInfo: {
                    phone: "",
                    password: "",
                }
            };
        },

        methods: {
            // 提交
            onSubmit() {
                if(!validForm.validateForm(this.userInfo)) {
                    return;
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/retrievePassword",
                    data: {
                        appkey: this.appkey,
                        phone: this.userInfo.phone,
                        password: this.userInfo.password,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "L001") {

                        this.$router.push({ name: "Login" });

                    } else{

                        Toast(res.data.msg);

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .van-nav-bar{
        height: 45px;
    }

    .van-form{
        margin-top: 50px;
    }
    .btn{
        margin: 30px 20px;
    }
</style>